.text-center {
    text-align: center;
}
.healthbar {
    width: 80%;
    height: 40px;
    background-color: #eee;
    margin: auto;
    -webkit-transition: width 500ms;
    transition: width 500ms;
}
.controls, .log {
    margin-top: 30px;
    text-align: center;
    padding: 10px;
    border: 1px solid #ccc;
    -webkit-box-shadow: 0px 3px 6px #ccc;
            box-shadow: 0px 3px 6px #ccc;
}
.turn {
    margin-top: 20px;
    margin-bottom: 20px;
    font-weight: bold;
    font-size: 22px;
}
.log ul {
    list-style: none;
    font-weight: bold;
    text-transform: uppercase;
}
.log ul li {
    margin: 5px;
}
.log ul .player-turn {
    color: blue;
    background-color: #e4e8ff;
}
.log ul .invader-turn {
    color: red;
